import 'jQuery';
const css = require('./master-prefix.css');
const $ = require('jQuery');
const moment = require('moment');
const tooltip = require('./Tooltip.js');
export default class Gantt {
    constructor(dataUrl, containerId) {
        this._name = 'Gantt-plugin';
        this._dataUrl = dataUrl;
        this.containerId = containerId;
        this._init();
    }
    _init() {
        $.ajax({
            dataType: 'json',
            url: this._dataUrl,
            context: this
        })
        .done(this._setData)
        .fail(this._dataError);
        this._setVariables();
    }
    _setVariables() {
        // called only once during initialization
        // default values set
        this._chartingColors = {
            pointer: 0,
            array: [
                '#3366CC', '#DC3912', '#FF9900',
                '#109618', '#990099', '#3B3EAC',
                '#0099C6', '#DD4477', '#66AA00',
                '#B82E2E', '#316395', '#994499',
                '#22AA99', '#AAAA11', '#6633CC',
                '#E67300', '#8B0707', '#329262',
                '#5574A6', '#3B3EAC' ],
            mutateMap: {},
            next(mutateKey) {
                let keyFound = false;
                keyFound = mutateKey in this.mutateMap;
                if (!keyFound) {
                    this.pointer++;
                    this.mutateMap[mutateKey] = this.array[this.pointer];
                    return this.array[this.pointer];
                }
                return this.mutateMap[mutateKey];
            }
        };
        this._setControlVariables();
    }
    _setControlVariables() {
        // should be called several times whenever gantt plugin control
        //  changes
        this.startDate = moment().startOf('week');
        this.rowHeight = 40;
    }
    get chartingColors() {
        return this._chartingColors;
    }
    get name() {
        return this._name;
    }
    get data() {
        return this._data;
    }
    get containerId() {
        return this._containerId;
    }
    get container() {
        return this._container;
    }
    get rowCount() {
        return this._rowCount ? this._rowCount : 0;
    }
    get startDate() {
        return this._startDate;
    }
    get endDate() {
        return this._endDate;
    }
    get rowHeight() {
        return this._rowHeight;
    }
    get $extraDataColTemplate() {
        const template = `
        <div class="flex-col"></div>
        `;
        return $.parseHTML(template);
    }
    get $rowData() {
        return $(`#row-${this.rowCount}`);
    }
    get dates() {
        let dateArr = {
            array: [],
            startDate: this.startDate.clone(),
            generate() {
                this.array[0] = 'Chart';
                for (let i = 1; i < 8; i++) {
                    this.array[i] = this.startDate.add(i > 1 ? 1 : 0, 'd').format('ll');
                }
                return this.array;
            }
        };
        return dateArr.generate();
    }
    set data(ajaxResponse) {
        this._data = ajaxResponse;
    }
    set containerId(id) {
        this._containerId = id;
        this._container = $('#' + this._containerId);
    }
    set rowCount(x) {
        this._rowCount = x;
    }
    set startDate(momentObj) {
        this._startDate = momentObj;
    }
    set endDate(momentObj) {
        this._endDate = momentObj;
    }
    set rowHeight(height) {
        this._rowHeight = height;
    }
    _rowTemplate(dataArr) {
        let template = '';
        // open flex-row <div>
        template += `<div id="row-${this.rowCount}" class="flex-row">`;
        // flex-item <div> with content
        for (let i = 0; i < 8; i++) {
            template += `<div id="row-${this.rowCount}-col-${i}" class="flex-item">`;
            template += `${dataArr[i]}`;
            template += `</div>`;
        }
        // close flex-row <div>
        template += `</div>`;
        return $.parseHTML(template);
    }
    _rowData(data) {
        let dataArr = {
            array: [],
            generate() {
                for (let i = 0; i < 8; i++) {
                    if (typeof data === 'undefined') {
                        data = [];
                    }
                    if (typeof data[i] !== 'undefined') {
                        this.array[i] = data[i];
                    } else {
                        this.array[i] = '';
                    }
                }
                return this.array;
            }
        };
        return dataArr.generate();
    }
    _extraDataItem(data) {
        const template = `
        <div class="flex-item-extra" data-tooltip title="${data.tooltip}">${data.id}</div>
        `;
        return $.parseHTML(template);
    }
    _setData(response) {
        this.data = response;
        this._render();
    }
    _dataError(response) {
        let error;
        if (typeof response.responseJSON === 'undefined') {
            error = 'empty JSON';
        } else {
            error = response.statusText;
        }
        throw new Error(`There was an error with the response: ${error}`);
    }
    _render() {
        this.container.addClass('flex-container');
        this._addTime();
        this.data.forEach(
            (elem, index, arr) => {
                this._addRow(elem);
            }
        );
        this._fillColors();
        // tooltip.refresh();
    }
    _addMomentData(rowIndex) {
        let startDateClone = this.startDate.clone();
        for (let i = 1; i < 8; i++) {
            $(`#row-${rowIndex}-col-${i}`).data('moment', startDateClone.add(i > 1 ? 1 : 0, 'd').toJSON());
        }
    }
    _addTime() {
        this.container.append(this._rowTemplate(this.dates));
        this._addMomentData(this.rowCount);
        this.rowCount++;
    }
    _addRow(data) {
        this.container.append(this._rowTemplate(this._rowData([data.desc])));
        this._addMomentData(this.rowCount);
        this.rowCount++;
    }
    _fillColors() {
        let row = 0;
        let col = 0;

        $.each(this.data, (dataRowIndex, dataOfRow) => {
            $.each(dataOfRow.bookings, (bookingIndex, booking) => {
                // C stands for confirmed.. for prototype only check this
                if (booking.st.trim() !== 'C') return;
                row = dataRowIndex + 1;
                for (col = 1; col < 8; col++) {
                    let cellDate = moment($(`#row-${row}-col-${col}`).data('moment'));
                    // refer to moment documentation about inclusivity of this method
                    //  http://momentjs.com/docs/#/query/is-between/
                    if (cellDate.isBetween(moment(booking.sd), moment(booking.ed), 'day', 'p[]')) {
                        let chartColor = this.chartingColors.next(booking.id);
                        let $cell = $(`#row-${row}-col-${col}`);
                        let cellData = $cell.data('cellData');
                        if (typeof cellData !== 'undefined') {
                            // it has been colored already, reset cell style, redraw structure
                            if (typeof $cell.attr('title') !== 'undefined' && $cell.attr('title').trim().length > 0) {
                                $cell.html('');
                                $cell.removeAttr('title');
                                $cell.removeAttr('data-tooltip');
                                $cell.css('background-color', '');
                                let doesExtraDataColExist = $cell.children('.flex-col').length > 0;
                                if (!doesExtraDataColExist) {
                                    $cell.append(this.$extraDataColTemplate);
                                }
                                let $extraDataCol = $cell.children('.flex-col');
                                chartColor = this.chartingColors.next(cellData[0]);
                                $extraDataCol.append(this._extraDataItem({
                                    id: cellData[0],
                                    tooltip: cellData[0]
                                }))
                                .find('div:last')
                                .css('background-color', chartColor);
                            }
                            chartColor = this.chartingColors.next(booking.id);
                            let $extraDataCol = $cell.children('.flex-col');
                            $extraDataCol.append(this._extraDataItem({
                                id: booking.id,
                                tooltip: booking.id
                            }))
                            .find('div:last')
                            .css('background-color', chartColor);
                            cellData.push(booking.id);
                            $cell.data('cellData', cellData);
                        } else {
                            // has not been colored before
                            $cell.css('background-color', chartColor);
                            $cell.text(booking.id);
                            $cell.data('cellData', [booking.id]);
                            $cell.attr('data-tooltip', '');
                            $cell.attr('title', booking.id);
                        }
                    }
                }
            });
        });
        this._refreshTooltip();
    }
    _refreshTooltip() {
        tooltip.refresh();
    }
}
module.exports = Gantt;



